import { Component } from 'react'


export default class Highlight extends Component {
	render() {
		const {
			position,
			onClick,
			onMouseOver,
		} = this.props

		return (
			<div>
				<div className="Highlight__parts">
					{position.rects.map((rect, index) => (
						<div
							onMouseOver={onMouseOver}
							onClick={onClick}
							key={index}
							style={rect}
							className={`Highlight__part`}
						/>
					))}
				</div>
			</div>
		)
	}
}
