import { Component, createRef } from 'react';
import PropTypes from 'prop-types';
import { defaultProps, propTypes } from '../components/PDFAnnotator.react';

import PDFHighlighter from './PDFHighlighter.react';
import PDFLoader from './PDFLoader.react';
import Highlight from './Highlight.react';


export default class PDFAnnotator extends Component {
  onAnnotationCreated(highlight) {
    this.props.setProps({
      highlights: [highlight, ...this.props.highlights]
    })
    this.triggerEvent('new_highlight', highlight)
  }

  onAnnotationClick(highlight) {
    this.triggerEvent('clicked_highlight', highlight)
  }

  onAnnotationHover(highlight) {
    this.triggerEvent('hovered_highlight', highlight)
  }

  triggerEvent(name, value) {
    this.props.setProps({
      event: { name, value, _: !this.props.event?._ }
    })
  }

  render() {
    return (
      <PDFLoader data={this.props.data}>
        {(pdfDocument) => (
          <PDFHighlighter
            height={this.props.height}
            width={this.props.width}
            pdfDocument={pdfDocument}
            onSelectionFinished={(position, text) => (
              this.onAnnotationCreated({ text, position })
            )}
            highlightTransform={(highlight, index) => (
              <Highlight
                position={{
                  ...highlight.position,
                  rects: highlight.position.rects.map(rect => {
                    return {
                      ...rect,
                      background: highlight.color || this.props.color || 'yellow'
                    }
                  })
                }}
                key={index}
                onClick={() => {
                  this.onAnnotationClick(this.props.highlights[index]);
                }}
                onMouseOver={() => {
                  this.onAnnotationHover(this.props.highlights[index]);
                }}
              />
            )}
            highlights={this.props.highlights}
          />
        )}
      </PDFLoader>
    );
  }
}

PDFAnnotator.defaultProps = defaultProps;
PDFAnnotator.propTypes = propTypes;
