import { PureComponent } from 'react'
import ReactDom from 'react-dom'

import {
  EventBus,
  PDFViewer,
  PDFLinkService,
  PDFFindController
} from 'pdfjs-dist/web/pdf_viewer'
import * as R from 'ramda'
window.R = R

import 'pdfjs-dist/legacy/web/pdf_viewer.css'
import '../styles.css'

const debounce = (callback, wait) => {
  let timeout
  return (...args) => {
    const context = this
    clearTimeout(timeout)
    timeout = setTimeout(() => callback.apply(context, args), wait)
  }
}

const getBoundingRect = clientRects => {
  const rects = Array.from(clientRects).map(rect => {
    const { left, top, width, height, page } = rect

    const X0 = left
    const X1 = left + width

    const Y0 = top
    const Y1 = top + height

    return { X0, X1, Y0, Y1, page }
  })

  let firstPage = Number.MAX_SAFE_INTEGER

  rects.forEach(rect => {
    firstPage = Math.min(
      firstPage,
      rect.page ?? firstPage
    )
  })

  const rectsWithSizeOnFirstPage = rects.filter(
    rect =>
      (rect.X0 > 0 || rect.X1 > 0 || rect.Y0 > 0 || rect.Y1 > 0) &&
      rect.page === firstPage
  )

  const optimal = rectsWithSizeOnFirstPage.reduce((res, rect) => {
    return {
      X0: Math.min(res.X0, rect.X0),
      X1: Math.max(res.X1, rect.X1),

      Y0: Math.min(res.Y0, rect.Y0),
      Y1: Math.max(res.Y1, rect.Y1),

      page: firstPage
    }
  }, rectsWithSizeOnFirstPage[0])

  const { X0, X1, Y0, Y1, page } = optimal

  return {
    left: X0,
    top: Y0,
    width: X1 - X0,
    height: Y1 - Y0,
    page
  }
}

const isClientRectInsidePageRect = (clientRect, pageRect) =>
  clientRect.top >= pageRect.top &&
  clientRect.bottom <= pageRect.bottom &&
  clientRect.right <= pageRect.right &&
  clientRect.left >= pageRect.left

const overlaps = (A, B) =>
  A.page === B.page &&
  A.left <= B.left &&
  B.left <= A.left + A.width

const sameLine = (A, B, yMargin = 5) =>
  A.page === B.page &&
  Math.abs(A.top - B.top) < yMargin &&
  Math.abs(A.height - B.height) < yMargin

const inside = (A, B) =>
  A.page === B.page &&
  A.top > B.top &&
  A.left > B.left &&
  A.top + A.height < B.top + B.height &&
  A.left + A.width < B.left + B.width

const nextTo = (A, B, xMargin = 10) => {
  const Aright = A.left + A.width
  const Bright = B.left + B.width

  return (
    A.page === B.page &&
    A.left <= B.left &&
    Aright <= Bright &&
    B.left - Aright <= xMargin
  )
}

const extendWidth = (A, B) => {
  A.width = Math.max(B.width - A.left + B.left, A.width)
}

const optimizeClientRects = clientRects => {
  const rects = clientRects.sort((A, B) => {
    const top = (A.page || 0) * A.top - (B.page || 0) * B.top
    return top === 0 ? A.left - B.left : top
  })

  const toRemove = new Set()

  const firstPass = rects.filter(rect => {
    return rects.every(otherRect => {
      return !inside(rect, otherRect)
    })
  })

  let passCount = 0

  while (passCount <= 2) {
    firstPass.forEach(A => {
      firstPass.forEach(B => {
        if (A === B || toRemove.has(A) || toRemove.has(B)) {
          return
        }

        if (!sameLine(A, B)) {
          return
        }

        if (overlaps(A, B)) {
          extendWidth(A, B)
          A.height = Math.max(A.height, B.height)

          toRemove.add(B)
        }

        if (nextTo(A, B)) {
          extendWidth(A, B)

          toRemove.add(B)
        }
      })
    })
    passCount += 1
  }

  return firstPass.filter(rect => !toRemove.has(rect))
}

const getClientRects = (range, pages, shouldOptimize = true) => {
  const clientRects = Array.from(range.getClientRects())

  const rects = []

  for (const clientRect of clientRects) {
    for (const page of pages) {
      const pageRect = page.node.getBoundingClientRect()

      if (
        isClientRectInsidePageRect(clientRect, pageRect) &&
        clientRect.top >= 0 &&
        clientRect.bottom >= 0 &&
        clientRect.width > 0 &&
        clientRect.height > 0 &&
        clientRect.width < pageRect.width &&
        clientRect.height < pageRect.height
      ) {
        const highlightedRect = {
          top: clientRect.top + page.node.scrollTop - pageRect.top,
          left: clientRect.left + page.node.scrollLeft - pageRect.left,
          width: clientRect.width,
          height: clientRect.height,
          page: page.number
        }

        rects.push(highlightedRect)
      }
    }
  }

  return shouldOptimize ? optimizeClientRects(rects) : rects
}

const getDocument = e => (e || {}).ownerDocument || document
const getWindow = e => (getDocument(e) || {}).defaultView || window
const isHTMLElement = e =>
  e instanceof HTMLElement || e instanceof getWindow(e).HTMLElement

const getPageFromElement = target => {
  const node = target.closest('.page')

  if (!node || !isHTMLElement(node)) {
    return null
  }

  const number = Number(node.dataset.pageNumber)

  return { node, number }
}

const getPagesFromRange = range => {
  const startParentElement = range?.startContainer?.parentElement
  const endParentElement = range?.endContainer?.parentElement

  if (!isHTMLElement(startParentElement) || !isHTMLElement(endParentElement)) {
    return []
  }

  const startPage = getPageFromElement(startParentElement)
  const endPage = getPageFromElement(endParentElement)

  if (!startPage?.number || !endPage?.number) {
    return []
  }

  if (startPage.number === endPage.number) {
    return [startPage]
  }

  if (startPage.number === endPage.number - 1) {
    return [startPage, endPage]
  }

  const pages = []

  let currentPage = startPage.number

  const document = startPage.node.ownerDocument

  while (currentPage <= endPage.number) {
    const currentPage = getPageFromElement(
      document.querySelector(`[data-page-number='${currentPage}'`)
    )
    if (currentPage) {
      pages.push(currentPage)
    }
  }

  return pages
}

export default class PDFHighlighter extends PureComponent {
  static defaultProps = {
    pdfScaleValue: 'auto'
  }

  state = {
    isCollapsed: true,
    range: null
  }

  eventBus = new EventBus()
  linkService = new PDFLinkService({
    eventBus: this.eventBus,
    externalLinkTarget: 2
  })

  resizeObserver = null
  containerNode = null
  unsubscribe = () => {}

  constructor(props) {
    super(props)
    if (typeof ResizeObserver !== 'undefined') {
      this.resizeObserver = new ResizeObserver(this.debouncedScaleValue)
    }
  }

  componentDidMount() {
    this.init()
  }

  attachRef = ref => {
    const { eventBus, resizeObserver: observer } = this
    this.containerNode = ref
    this.unsubscribe()

    if (ref) {
      const { ownerDocument: doc } = ref
      eventBus.on('textlayerrendered', this.onTextLayerRendered)
      eventBus.on('pagesinit', this.onDocumentReady)
      doc.addEventListener('selectionchange', this.onSelectionChange)
      doc.addEventListener('keydown', this.handleKeyDown)
      doc.defaultView?.addEventListener('resize', this.debouncedScaleValue)
      if (observer) observer.observe(ref)

      this.unsubscribe = () => {
        eventBus.off('pagesinit', this.onDocumentReady)
        eventBus.off('textlayerrendered', this.onTextLayerRendered)
        doc.removeEventListener('selectionchange', this.onSelectionChange)
        doc.removeEventListener('keydown', this.handleKeyDown)
        doc.defaultView?.removeEventListener('resize', this.debouncedScaleValue)
        if (observer) observer.disconnect()
      }
    }
  }

  componentDidUpdate(prevProps) {
    if (prevProps.pdfDocument !== this.props.pdfDocument) {
      this.init()
    } else if (prevProps.highlights !== this.props.highlights) {
      this.renderHighlights(this.props)
    }
  }

  init() {
    const { pdfDocument } = this.props

    this.findController = 
      this.findController ||
      new PDFFindController({
        linkService: this.linkService,
        eventBus: this.eventBus
      })

    this.viewer =
      this.viewer ||
      new PDFViewer({
        container: this.containerNode,
        eventBus: this.eventBus,
        findController: this.findController,
        textLayerMode: 2,
        removePageBorders: true,
        linkService: this.linkService,
        renderer: 'canvas',
        l10n: null
      })

    this.linkService.setDocument(pdfDocument)
    this.linkService.setViewer(this.viewer)
    this.viewer.setDocument(pdfDocument)

    console.log('highligher', this)
  }

  componentWillUnmount() {
    this.unsubscribe()
  }

  findOrCreateHighlightLayer(page) {
    const { textLayer } = this.viewer.getPageView(page - 1) || {}

    if (!textLayer) {
      return null
    }

    let containerLayer = textLayer.textLayerDiv.querySelector(
      '.PdfHighlighter__highlight-layer'
    )

    if (!containerLayer) {
      containerLayer = getDocument(textLayer.textLayerDiv).createElement('div')
      containerLayer.className = '.PdfHighlighter__highlight-layer'
      textLayer.textLayerDiv.appendChild(containerLayer)
    }

    return containerLayer
  }

  renderHighlights(nextProps) {
    const { highlightTransform, highlights } = nextProps || this.props

    const { pdfDocument } = this.props

    const highlightsByPage = R.groupBy(
      R.path(['position', 'page']),
      R.filter(Boolean, highlights)
    )

    for (let page = 1; page <= pdfDocument.numPages; page++) {
      const highlightLayer = this.findOrCreateHighlightLayer(page)

      if (highlightLayer) {
        ReactDom.render(
          <div>
            {(highlightsByPage[String(page)] || []).map(
              ({ position, id, ...highlight }, index) => {
                return highlightTransform(
                  {
                    id,
                    position: position,
                    ...highlight
                  },
                  index
                )
              }
            )}
          </div>,
          highlightLayer
        )
      }
    }
  }

  onTextLayerRendered = () => {
    this.renderHighlights()
  }

  onDocumentReady = () => {
    this.handleScaleValue()
  }

  onSelectionChange = () => {
    const container = this.containerNode
    const selection = getWindow(container).getSelection()

    if (!selection) {
      return
    }

    const range = selection.rangeCount > 0 ? selection.getRangeAt(0) : null

    if (selection.isCollapsed) {
      this.setState({ isCollapsed: true })
      return
    }

    if (
      !range ||
      !container ||
      !container.contains(range.commonAncestorContainer)
    ) {
      return
    }

    this.setState({
      isCollapsed: false,
      range
    })

    this.debouncedAfterSelection()
  }

  onScroll = () => {
    this.renderHighlights()

    this.viewer.container.removeEventListener('scroll', this.onScroll)
  }

  onMouseDown = event => {
    if (!isHTMLElement(event.target)) {
      return
    }
  }

  afterSelection = () => {
    const { isCollapsed, range } = this.state
    const pages = getPagesFromRange(range)
    const rects = getClientRects(range, pages)

    if (
      !range ||
      isCollapsed ||
      !pages ||
      pages.length == 0 ||
      rects.length === 0
    )
      return

    const boundingRect = getBoundingRect(rects)

    this.props.onSelectionFinished(
      {
        page: pages[0].number,
        boundingRect,
        rects
      },
      range.toString()
    )
  }

  debouncedAfterSelection = debounce(this.afterSelection, 500)

  handleScaleValue = () => {
    if (this.viewer) {
      this.viewer.currentScaleValue = this.props.pdfScaleValue
    }
  }

  debouncedScaleValue = debounce(this.handleScaleValue, 500)

  render() {
    return (
      <div onPointerDown={this.onMouseDown}>
        <div
          ref={this.attachRef}
          className="PdfHighlighter"
          onContextMenu={e => e.preventDefault()}
          style={{
            height: this.props.height,
            width: this.props.width
          }}
        >
          <div className="pdfViewer" />
        </div>
      </div>
    )
  }
}
